/**
 * Index
 * @summary The home page of our website.
 */

import React, { useState, useEffect } from 'react';
import Layout from '../components/Layout';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import Button from 'react-bootstrap/Button';

const Home = props => {
    return (
        <Layout>
            {/* <h1>Media Tracker</h1>
            <p>Home page</p> */}
            <p>{`Res: ${props.result}`}</p>
            <br />
            <LoadingButton>Check in</LoadingButton>
        </Layout>
    );
};

const displayNames = props => {
    return (
        <p>{props.names}</p>
    );
}

const LoadingButton = () => {
    const [isLoading, setLoading] = useState(false);
    const [isClicked, setClicked] = useState(false);
    var data = "";

    useEffect(() => {
        if (isLoading) {
            checkIn().then(props => {
                console.log(`Before: ${data}`)
                data = props.result;
                console.log(`After: ${data}`)
                setClicked(true);
                setLoading(false);
            });
        }
    }, [isLoading]);

    const handleClick = () => setLoading(true);

    return (
        <div>
            <Button
                variant="primary"
                disabled={isLoading}
                onClick={!isLoading ? handleClick : null}
            >
                {isLoading ? 'Loadingâ€¦' : 'Click to load'}
            </Button>
            {
                isClicked ?
                    <p><br />{data}</p>
                    :
                    null
            }
        </div>
    );
}

const checkIn = async () => {
    try {
        const res = await axios.get('http://localhost:5000/check-fabian');
        return { result: res.data.Success }
    } catch (error) {
        console.error(error);
        return { result: 'Failed to check in members' }
    }
}

Home.getInitialProps = async () => {
    try {
        // call server at endpoint /search-title
        const res = await axios.get('http://localhost:5000/ping');
        // res.data is the JSON object returned which is { Success: ***data*** }
        return { result: res.data.Success }
    } catch (error) {
        console.error(error);
        return { result: 'Failed to fetch data from server' }
    }
};

export default Home;